<template>
  <el-card class="main-card">
    <div class="title">{{ this.$route.name }}</div>

    <!-- -->
    <div class="operation-container">
      <el-button
          type="danger"
          icon="el-icon-delete"
          size="small"
          :disabled="multiple"
          @click="handleDelete">
        
      </el-button>

      <!--  -->
      <div style="margin-left:auto">
        <el-input
            v-model="queryParams.keywords"
            prefix-icon="el-icon-search"
            size="small"
            placeholder=""
            style="width:200px"
            clearable
            @clear="getRecordData"/>

        <el-select v-model="queryParams.status" style="margin-left: 10px" size="small" placeholder=""
                   clearable>
          <el-option :key="'0'" label="" :value="'0'"/>
          <el-option :key="'1'" label="" :value="'1'"/>
          <el-option :key="'2'" label="" :value="'2'"/>
        </el-select>

        <el-button
            type="primary"
            size="small"
            icon="el-icon-search"
            style="margin-left:1rem"
            @click="getRecordData">
          
        </el-button>
      </div>
    </div>


    <!-- -->
    <el-table
        :data="recordTableData"
        :header-cell-style="{background:'#eef0f1'}"
        @selection-change="handleSelectionChange"
        style="width: 100%">

      <el-table-column type="selection" width="55" align="center"/>

      <el-table-column label="id" prop="id" align="center"/>

      <el-table-column
          label=""
          prop="name"
          align="center"
          :show-overflow-tooltip="true"
      />

      <el-table-column
          label=""
          prop="remark"
          align="center"
          :show-overflow-tooltip="true"
      />

      <el-table-column
          label=""
          align="center"
      >

        <template slot-scope="scope">
          <el-image
              style="width: 88px; height: 88px"
              :src="scope.row.imgUrl"
              ref="preview"
              @click="imgMagnify(scope.row.imgUrl)"
              :preview-src-list="srcList"
              fit="contain"/>
        </template>

      </el-table-column>

      <el-table-column
          label=""
          prop="resultName"
          align="center"
          :show-overflow-tooltip="true"
      />

      <el-table-column
          label=""
          prop="longitude"
          align="center"
      />

      <el-table-column
          label=""
          prop="latitude"
          align="center"
      />

      <el-table-column
          label="openId"
          prop="createBy"
          align="center"
      />

      <el-table-column
          label=""
          align="center"
      >

        <template slot-scope="scope">
          <el-tag v-if="scope.row.status==0" type="success"></el-tag>
          <el-tag v-if="scope.row.status==1" type="danger"></el-tag>
          <el-tag v-if="scope.row.status==2" type="warning"></el-tag>
        </template>
      </el-table-column>


      <el-table-column prop="createTime" label="" align="center"/>

      <el-table-column label="" align="center" width="180">
        <template slot-scope="scope">

          <el-button type="text" @click="handleUpdate(scope.row,'0')" v-if="scope.row.status == '2'">
            
          </el-button>

          <el-button type="text" @click="handleUpdate(scope.row,'1')" v-if="scope.row.status == '2'">
            
          </el-button>

          <el-button type="text" slot="reference" style="color: red" @click="handleDelete(scope.row)">
            
          </el-button>

        </template>
      </el-table-column>
    </el-table>

    <el-pagination
        class="pagination-container"
        :page-size.sync="queryParams.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        :page-sizes="[10, 15, 20, 50]"
        :current-page.sync="queryParams.pageNum"
        @current-change="getRecordData"
        @size-change="getRecordData"
    />
  </el-card>
</template>

<script>
import {deleteRecord, getRecordDataList, updateRecord} from "@/api/record";
import {updateUser} from "@/api/user";

export default {
  name: "Record",
  data() {
    return {
      //
      recordTableData: [],
      // 
      multiple: true,
      // 
      queryParams: {
        pageNum: 1,
        pageSize: 10,
        keywords: '',
        status: ''
      },
      //
      total: 0,
      srcList: []
    }
  },

  created() {
    this.getRecordData()
  },

  methods: {
    //
    async getRecordData() {
      const {data: res} = await getRecordDataList(this.queryParams)
      this.recordTableData = res.data.rows
      this.total = Number(res.data.total)
    },

    // 
    handleSelectionChange(selection) {
      this.ids = selection.map((item) => item.id)
      this.multiple = !selection.length
    },

    async handleUpdate(row, status) {
      row.status = status
      await updateRecord(row)

      this.$notify({
        title: '',
        message: '',
        type: 'success'
      });
      await this.getRecordData()
    },


    //
    imgMagnify(img) {
      if (this.srcList.length > 0) {
        this.srcList.length = 0
      }
      this.srcList.push(img)
    },

    //
    async handleDelete(row) {
      const ids = row.id || this.ids

      try {
        const del = await this.$confirm('ID"' + ids + '"', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })

        if (del == 'confirm') {
          await deleteRecord(ids)

          await this.getRecordData()
          this.$notify({
            title: '',
            message: '',
            type: 'success'
          });
        }

      } catch (e) {
        this.$message({
          type: 'info',
          message: ''
        });
      }

    },
  }
}
</script>

