<template>
  <el-card class="main-card">
    <div class="title">{{ this.$route.name }}</div>

    <!-- -->
    <div class="operation-container">
      <el-button
          type="primary"
          size="small"
          icon="el-icon-plus"
          @click="handleAdd">
        
      </el-button>

      <el-button
          type="danger"
          icon="el-icon-delete"
          size="small"
          :disabled="multiple"
          @click="handleDelete">
        
      </el-button>

      <!--  -->
      <div style="margin-left:auto">
        <el-input
            v-model="queryParams.keywords"
            prefix-icon="el-icon-search"
            size="small"
            placeholder=""
            style="width:200px"
            clearable
            @clear="getUsers"/>
        <el-button
            type="primary"
            size="small"
            icon="el-icon-search"
            style="margin-left:1rem"
            @click="getUsers">
          
        </el-button>
      </div>
    </div>

    <!-- -->
    <el-table
        :data="userTableData"
        :header-cell-style="{background:'#eef0f1'}"
        @selection-change="handleSelectionChange"
        style="width: 100%">

      <el-table-column type="selection" width="55" align="center"/>

      <el-table-column label="id" prop="id" align="center"/>

      <el-table-column
          label=""
          prop="userName"
          align="center"
          :show-overflow-tooltip="true"
      />

      <el-table-column
          label=""
          prop="nickName"
          align="center"
          :show-overflow-tooltip="true"
      />


      <el-table-column label="" align="center">
        <template slot-scope="scope">
          <el-switch
              v-model="scope.row.status"
              active-value="0"
              inactive-value="1"
              @change="handleStatusChange(scope.row)"
          />
        </template>
      </el-table-column>

      <el-table-column prop="createTime" label="" align="center"/>

      <el-table-column label="" align="center">
        <template slot-scope="scope">
          <el-button type="text" @click="handleUpdate(scope.row)">
            
          </el-button>

          <el-button type="text" slot="reference" style="color: red" @click="handleDelete(scope.row)">
            
          </el-button>

        </template>
      </el-table-column>
    </el-table>

    <el-pagination
        class="pagination-container"
        :page-size.sync="queryParams.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        :page-sizes="[10, 15, 20, 50]"
        :current-page.sync="queryParams.pageNum"
        @current-change="getUsers"
        @size-change="getUsers"
    />

    <!--  -->
    <el-dialog :title="title" :visible.sync="open" width="600px" append-to-body>
      <el-form ref="form" :model="form" :rules="rules" label-width="80px">

        <el-row>
          <el-col :span="24">
            <el-form-item label="" prop="nickName">
              <el-input
                  v-model="form.nickName"
                  placeholder=""
                  maxlength="30"
              />
            </el-form-item>
          </el-col>
        </el-row>


        <el-row>
          <el-col :span="12">
            <el-form-item
                v-if="form.id == null"
                label=""
                prop="userName"
            >
              <el-input
                  v-model="form.userName"
                  placeholder=""
                  maxlength="30"
              />
            </el-form-item>
          </el-col>

          <el-col :span="12">
            <el-form-item
                label=""
                prop="password"
            >
              <el-input
                  v-model="form.password"
                  placeholder=""
                  type="password"
                  maxlength="20"
                  show-password
              />
            </el-form-item>
          </el-col>
        </el-row>

        <el-row>
          <el-col :span="12">
            <el-form-item label="">
              <el-radio-group v-model="form.status">
                <el-radio :key="'0'" :label="'0'"></el-radio>
                <el-radio :key="'1'" :label="'1'"></el-radio>
              </el-radio-group>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row>
          <el-col :span="24">
            <el-form-item label="">

              <el-select v-model="form.roleIds" multiple placeholder="">

                <el-option
                    v-for="item in roleOptions"
                    :key="item.id"
                    :label="item.roleName"
                    :value="item.id"
                    :disabled="item.status == 1"
                />
              </el-select>


            </el-form-item>
          </el-col>
        </el-row>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitForm"> </el-button>
        <el-button @click="cancel"> </el-button>
      </div>
    </el-dialog>


  </el-card>
</template>

<script>
import {addUser, deleteUser, getUserBy, getUserList, updateUser} from "@/api/user";
import {getRoleOptions} from "@/api/role";

export default {
  name: "User",
  data() {
    return {
      // 
      open: false,
      // 
      multiple: true,
      // 
      title: '',
      // 
      roleOptions: [],
      // 
      ids: [],
      // 
      form: {},
      //
      total: 0,
      //
      userTableData: [],
      // 
      rules: {
        userName: [
          {required: true, message: '', trigger: 'blur'},
        ],
        nickName: [
          {required: true, message: '', trigger: 'blur'}
        ],
        password: [
          {required: true, message: '', trigger: 'blur'},
          {
            min: 6,
            max: 18,
            message: ' 5  18 ',
            trigger: 'blur'
          }
        ],
      },
      // 
      queryParams: {
        pageNum: 1,
        pageSize: 10,
        keywords: '',
      },
    }
  },

  created() {
    this.getUsers()
    this.getRoleOptions()
  },

  methods: {

    //
    async getUsers() {
      const {data: res} = await getUserList(this.queryParams)
      this.userTableData = res.data.rows
      this.total = Number(res.data.total)
    },

    //
    async getRoleOptions() {
      const {data: res} = await getRoleOptions()
      this.roleOptions = res.data
    },

    //
    async handleAdd() {
      this.reset()

      this.open = true
      this.title = ''
    },

    // 
    handleSelectionChange(selection) {
      this.ids = selection.map((item) => item.id)
      this.multiple = !selection.length
    },

    //
    async handleStatusChange(row) {
      await updateUser(row)
      this.$notify({
        title: '',
        message: '',
        type: 'success'
      });
      await this.getUsers()
    },


    //
    async handleUpdate(row) {
      this.reset()
      const id = row.id

      //
      const {data: res} = await getUserBy({id: id})
      this.form = res.data
      this.open = true
      this.title = ''
    },

    //
    async handleDelete(row) {
      const ids = row.id || this.ids

      try {
        const del = await this.$confirm('ID"' + ids + '"', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })

        if (del == 'confirm') {
          await deleteUser(ids)
          console.log(ids)
          await this.getUsers()
          this.$notify({
            title: '',
            message: '',
            type: 'success'
          });
        }

      } catch (e) {
        this.$message({
          type: 'info',
          message: ''
        });
      }
    },


    // 
    cancel() {
      this.open = false
      // this.reset()
    },

    // 
    reset() {
      this.form = {
        id: null,
        userName: null,
        nickName: null,
        password: null,
        status: '0',
        roleIds: []
      }
      this.resetForm('form')
    },

    //
    resetForm(formName) {
      if (this.$refs.form) {
        this.$nextTick(() => {
          this.$refs[formName].resetFields();
        })
      }
    },

    //
    submitForm() {
      this.$refs['form'].validate(async (valid) => {
        if (valid) {
          if (this.form.id) {

            await updateUser(this.form)
            this.$notify({
              title: '',
              message: '',
              type: 'success'
            });

            this.open = false
            await this.getUsers()

          } else {
            await addUser(this.form)
            this.$notify({
              title: '',
              message: '',
              type: 'success'
            });
            this.open = false
            await this.getUsers()
          }
        }
      })
    }
  }
}
</script>

