<template>
  <el-card class="main-card">
    <div class="title">{{ this.$route.name }}</div>

    <!-- -->
    <div class="operation-container">
      <el-button
          type="primary"
          size="small"
          icon="el-icon-plus"
          @click="handleAdd">
        
      </el-button>

      <el-button
          type="danger"
          icon="el-icon-delete"
          size="small"
          :disabled="multiple"
          @click="handleDelete">
        
      </el-button>

      <!--  -->
      <div style="margin-left:auto">
        <el-input
            v-model="queryParams.keywords"
            prefix-icon="el-icon-search"
            size="small"
            placeholder=""
            style="width:200px"
            clearable
            @clear="getRoles"/>
        <el-button
            type="primary"
            size="small"
            icon="el-icon-search"
            style="margin-left:1rem"
            @click="getRoles">
          
        </el-button>
      </div>
    </div>

    <!-- -->
    <el-table
        :data="roleTableData"
        :header-cell-style="{background:'#eef0f1'}"
        @selection-change="handleSelectionChange"
        style="width: 100%">

      <el-table-column type="selection" width="55" align="center"/>

      <el-table-column label="id" prop="id" align="center"/>
      <el-table-column
          label=""
          prop="roleName"
          align="center"
          :show-overflow-tooltip="true"
      />

      <el-table-column
          label=""
          prop="roleKey"
          align="center"
          :show-overflow-tooltip="true"
      >
        <template slot-scope="scope">
          <el-tag type="danger">{{scope.row.roleKey}}</el-tag>
        </template>
      </el-table-column>

      <el-table-column label="" prop="roleSort" align="center"/>

      <el-table-column label="" align="center">
        <template slot-scope="scope">
          <el-switch
              v-model="scope.row.status"
              active-value="0"
              inactive-value="1"
              @change="handleStatusChange(scope.row)"
          />
        </template>
      </el-table-column>

      <el-table-column prop="createTime" label="" align="center"/>

      <el-table-column label="" align="center">
        <template slot-scope="scope">
          <el-button type="text" @click="handleUpdate(scope.row)">
            
          </el-button>

          <el-button type="text" slot="reference" style="color: red" @click="handleDelete(scope.row)">
            
          </el-button>

        </template>
      </el-table-column>
    </el-table>

    <el-pagination
        class="pagination-container"
        :page-size.sync="queryParams.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        :page-sizes="[10, 15, 20, 50]"
        :current-page.sync="queryParams.pageNum"
        @current-change="getRoles"
        @size-change="getRoles"
    />

    <!--  -->
    <el-dialog :title="title" :visible.sync="open" width="500px" append-to-body>
      <el-form ref="form" :model="form" :rules="rules" label-width="100px">
        <el-form-item label="" prop="roleName">
          <el-input v-model="form.roleName" placeholder=""/>
        </el-form-item>

        <el-form-item prop="roleKey">
          <span slot="label">
            <el-tooltip
                content="@PreAuthorize(`@ss.hasRole('admin')`)"
                placement="top"
            >
              <i class="el-icon-question"/>
            </el-tooltip>
            
          </span>
          <el-input v-model="form.roleKey" placeholder=""/>
        </el-form-item>

        <el-form-item label="" prop="roleSort">
          <el-input-number
              v-model="form.roleSort"
              controls-position="right"
              :min="0"
          />
        </el-form-item>

        <el-form-item label="">
          <el-radio-group v-model="form.status">
            <el-radio :key="'0'" :label="'0'"></el-radio>
            <el-radio :key="'1'" :label="'1'"></el-radio>
          </el-radio-group>
        </el-form-item>

        <el-form-item label="">
          <el-checkbox
              v-model="menuExpand"
              @change="handleCheckedTreeExpand($event)"
          >/
          </el-checkbox>
          <el-checkbox
              v-model="menuNodeAll"
              @change="handleCheckedTreeNodeAll($event)"
          >/
          </el-checkbox>

          <el-tree
              ref="menu"
              class="tree-border"
              :data="menuOptions"
              show-checkbox
              node-key="id"
              :check-strictly="false"
              empty-text=""
              :props="defaultProps"
          />

        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitForm"> </el-button>
        <el-button @click="cancel"> </el-button>
      </div>
    </el-dialog>

  </el-card>
</template>

<script>
import {addRole, deleteRole, getRoleBy, getRoleList, updateRole} from "@/api/role";
import {getMenuList, getRoleMenuIds} from "@/api/menu";
import {createTree} from "@/utils/createTree";

export default {
  name: "Role",
  data() {
    return {
      // 
      queryParams: {
        pageNum: 1,
        pageSize: 10,
        keywords: '',
      },
      // 
      total: 0,
      // 
      multiple: true,
      //
      roleTableData: [],
      // 
      title: '',
      // 
      open: false,
      // 
      menuOptions: [],
      // 
      ids: [],
      // 
      form: {},
      defaultProps: {
        children: 'children',
        label: 'menuName'
      },
      menuExpand: false,
      menuNodeAll: false,
      // 
      rules: {
        roleName: [
          {required: true, message: '', trigger: 'blur'}
        ],
        roleKey: [
          {required: true, message: '', trigger: 'blur'}
        ]
      }

    }
  },

  created() {
    this.getRoles()
  },

  methods: {
    //
    async getRoles() {
      const {data: res} = await getRoleList(this.queryParams)
      this.roleTableData = res.data.rows
      this.total =  Number(res.data.total)
    },

    //
    handleAdd() {
      this.reset()
      this.getTreeSelect()
      this.open = true
      this.title = ''
    },

    //
    async getTreeSelect() {
      const {data: res} = await getMenuList()
      this.menuOptions = createTree(res.data, 'id')
    },


    //
    async handleStatusChange(row) {
      await updateRole(row)
      this.$notify({
        title: '',
        message: '',
        type: 'success'
      });
      await this.getRoles()
    },

    //
    async handleUpdate(row) {
      this.reset()
      await this.getTreeSelect()
      const {data: res} = await getRoleBy({id: row.id})
      this.form = res.data
      this.open = true
      this.$nextTick(async () => {
        const {data: res} = await getRoleMenuIds({id: row.id})
        res.data.forEach(value => {
          this.$refs.menu.setChecked(value, true, false);
        })
      })
      this.title = ''
    },

    //
    submitForm() {
      this.$refs['form'].validate(async (valid) => {
        if (valid) {
          if (this.form.id !== null) {
            this.form.menuIds = this.getMenuAllCheckedKeys()
            await updateRole(this.form)
            this.$notify({
              title: '',
              message: '',
              type: 'success'
            });
            this.open = false
            await this.getRoles()
          } else {
            this.form.menuIds = this.getMenuAllCheckedKeys()
            await addRole(this.form)
            this.$notify({
              title: '',
              message: '',
              type: 'success'
            });
            this.open = false
            await this.getRoles()
          }
        }
      })
    },


    //
    async handleDelete(row) {
      const ids = row.id || this.ids

      try {
        const del = await this.$confirm('ID"' + ids + '"', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })

        if (del == 'confirm') {
          await deleteRole(ids)
          await this.getRoles()
          this.$notify({
            title: '',
            message: '',
            type: 'success'
          });
        }

      } catch (e) {
        this.$message({
          type: 'info',
          message: ''
        });
      }
    },

    // 
    getMenuAllCheckedKeys() {
      // 
      const checkedKeys = this.$refs.menu.getCheckedKeys()
      // 
      const halfCheckedKeys = this.$refs.menu.getHalfCheckedKeys()
      checkedKeys.unshift.apply(checkedKeys, halfCheckedKeys)
      return checkedKeys
    },

    // /
    handleCheckedTreeExpand(value) {
      const treeList = this.menuOptions
      for (let i = 0; i < treeList.length; i++) {
        this.$refs.menu.store.nodesMap[treeList[i].id].expanded = value
      }
    },
    // /
    handleCheckedTreeNodeAll(value) {
      this.$refs.menu.setCheckedNodes(value ? this.menuOptions : [])
    },

    // 
    handleSelectionChange(selection) {
      this.ids = selection.map((item) => item.id)
      this.multiple = !selection.length
    },

    // 
    reset() {
      if (this.$refs.menu) {
        this.$refs.menu.setCheckedKeys([])
      }
      this.menuNodeAll = false
      this.menuNodeAll = false
      this.menuExpand = false

      this.form = {
        id: null,
        roleName: null,
        roleKey: null,
        roleSort: 0,
        status: '0',
        menuIds: [],
      }

      this.resetForm('form')
    },

    // 
    cancel() {
      this.open = false
      this.reset()
    },

    //
    resetForm(formName) {
      if (this.$refs.form) {
        this.$nextTick(() => {
          this.$refs[formName].resetFields();
        })
      }
    }


  }
}
</script>

