import request from '@/utils/request'

//
export function getWxUserDataList(params) {
    return request({
        url: `/wxUser/list?pageNum=${params.pageNum}&pageSize=${params.pageSize}&keywords=${params.keywords}`,
        method: 'get',
    })
}


//
export function updateWxUser(data) {
    return request({
        url: '/wxUser',
        method: 'put',
        data: data
    })
}