<template>
  <el-card class="main-card">
    <div class="title">{{ this.$route.name }}</div>
    <!-- -->
    <div class="operation-container">
      <el-button
          type="primary"
          size="small"
          icon="el-icon-plus"
          @click="handleAdd">
        
      </el-button>

      <!--  -->
      <div style="margin-left:auto">
        <el-input
            v-model="keywords"
            prefix-icon="el-icon-search"
            size="small"
            placeholder=""
            style="width:200px"
            clearable
            @clear="getMenus"/>
        <el-button
            type="primary"
            size="small"
            icon="el-icon-search"
            style="margin-left:1rem"
            @click="getMenus">
          
        </el-button>
      </div>
    </div>

    <!-- -->
    <el-table
        :data="menuTableData"
        :header-cell-style="{background:'#eef0f1'}"
        row-key="id"
        :tree-props="{children: 'children', hasChildren: 'hasChildren'}"
        style="width: 100%">

      <!--  -->
      <el-table-column prop="menuName" :show-overflow-tooltip="true" label=""/>

      <!-- -->
      <el-table-column prop="icon" label="" align="center">
        <template slot-scope="scope ">
          <svg-icon :icon-class="scope.row.icon" v-if="scope.row.icon" style="width: 20px; height: 20px"/>
          <span v-else>-</span>
        </template>
      </el-table-column>

      <!-- -->
      <el-table-column prop="menuType" label="" align="center">
        <template slot-scope="scope">
          <el-tag v-if="scope.row.menuType==='M'"></el-tag>
          <el-tag type="warning" v-else-if="scope.row.menuType==='C'"></el-tag>
          <el-tag type="danger" v-else-if="scope.row.menuType==='F'"></el-tag>
        </template>
      </el-table-column>

      <el-table-column prop="orderNum" label="" align="center"/>

      <el-table-column prop="perms" label="" align="center">
        <template slot-scope="scope">
          {{ scope.row.perms ? scope.row.perms : '-' }}
        </template>
      </el-table-column>

      <el-table-column prop="path" label="" align="center">
        <template slot-scope="scope">
          {{ scope.row.path ? scope.row.path : '-' }}
        </template>
      </el-table-column>

      <el-table-column prop="component" label="" align="center">
        <template slot-scope="scope">
          {{ scope.row.component ? scope.row.component : '-' }}
        </template>
      </el-table-column>

      <el-table-column prop="status" label="" width="80">
        <template slot-scope="scope">
          <el-tag v-if="scope.row.status==0" type="success"></el-tag>
          <el-tag v-if="scope.row.status==1" type="danger"></el-tag>
        </template>
      </el-table-column>

      <el-table-column prop="createTime" label="" align="center"/>
      <el-table-column label="" align="center">
        <template slot-scope="scope">
          <el-button type="text" @click="handleUpdate(scope.row)">
            
          </el-button>

          <el-popconfirm
              title=""
              style="margin-left:10px"
              @confirm="handleDelete(scope.row)">
            <el-button type="text" slot="reference" style="color: red">
              
            </el-button>
          </el-popconfirm>

        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <el-dialog :title="title" :visible.sync="open" width="680px" append-to-body>
      <el-form ref="form" :model="form" :rules="rules" label-width="100px">
        <el-row>
          <el-col :span="24">
            <el-form-item label="">
              <TreeSelect
                  v-model="form.parentId"
                  :options="menuOptions"
                  :normalizer="normalizer"
                  :show-count="true"
                  placeholder=""
              />
            </el-form-item>
          </el-col>
          <el-col :span="24">
            <el-form-item label="" prop="menuType">
              <el-radio-group v-model="form.menuType" @change="menuTypeChange">
                <el-radio label="M"></el-radio>
                <el-radio label="C"></el-radio>
                <el-radio label="F"></el-radio>
              </el-radio-group>
            </el-form-item>
          </el-col>
          <el-col :span="24">
            <el-form-item v-if="form.menuType != 'F'" label="">
              <el-select
                  v-model="form.icon"
                  placeholder=""
                  clearable
                  size="small"
                  style="width: 240px"
              >
                <el-option v-for="(item, index) in icons" :key="index" :value="item">
                  <svg-icon :icon-class="item"/>
                  <span style="margin-left: 8px">{{ item }}</span>
                </el-option>
              </el-select>

            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="menuName">
              <el-input v-model="form.menuName" placeholder=""/>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="orderNum">
              <el-input-number v-model="form.orderNum" controls-position="right" :min="0"/>
            </el-form-item>
          </el-col>
          <el-col :span="24">
            <el-form-item v-if="form.menuType != 'F' && form.menuType != 'M'" prop="path">
              <span slot="label">
                <el-tooltip content="`/user`" placement="top">
                  <i class="el-icon-question"/>
                </el-tooltip>
                
              </span>
              <el-input v-model="form.path" placeholder=""/>
            </el-form-item>
          </el-col>
          <el-col v-if="form.menuType == 'C'" :span="12">
            <el-form-item prop="component">
              <span slot="label">
                <el-tooltip content="`/User``views`" placement="top">
                  <i class="el-icon-question"/>
                </el-tooltip>
                
              </span>
              <el-input v-model="form.component" placeholder=""/>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item v-if="form.menuType != 'M' && form.menuType != 'C'">
              <el-input v-model="form.perms" placeholder="" maxlength="100"/>
              <span slot="label">
                <el-tooltip content=""
                            placement="top">
                  <i class="el-icon-question"/>
                </el-tooltip>
                
              </span>
            </el-form-item>
          </el-col>

          <el-col :span="12">
            <el-form-item v-if="form.menuType != 'F'">
              <span slot="label">
                <el-tooltip content="" placement="top">
                  <i class="el-icon-question"/>
                </el-tooltip>
                
              </span>
              <el-radio-group v-model="form.status">
                <el-radio :key="'0'" :label="'0'"></el-radio>
                <el-radio :key="'1'" :label="'1'"></el-radio>
              </el-radio-group>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitForm"> </el-button>
        <el-button @click="cancel"> </el-button>
      </div>
    </el-dialog>

  </el-card>
</template>

<script>
import {
  handleAdd,
  handleDelete,
  getMenuBy,
  getMenuList,
  handleUpdate,
  addMenu,
  updateMenu,
  deleteMenu
} from "@/api/menu";
import {createTree} from "@/utils/createTree";
import TreeSelect from '@riophae/vue-treeselect'
import '@riophae/vue-treeselect/dist/vue-treeselect.css'

export default {
  name: "Menu",
  components: {TreeSelect},
  data() {
    return {
      //
      keywords: '',
      //
      menuTableData: [],
      // 
      title: '',
      // 
      form: {},
      // 
      open: false,
      // 
      menuOptions: [],
      //
      icons: [],
      // 
      rules: {
        menuName: [
          {required: true, message: '', trigger: 'blur'}
        ],
        orderNum: [
          {required: true, message: '', trigger: 'blur'}
        ],
        path: [
          {required: true, message: '', trigger: 'blur'}
        ],
        component: [
          {required: true, message: '', trigger: 'blur'}
        ]
      }
    }
  },

  created() {
    this.getMenus()
    this.icons = this.getIcons()
  },

  methods: {
    //
    async getMenus() {
      const {data: res} = await getMenuList({keywords: this.keywords})
      if (res) {
        //
        this.menuTableData = createTree(res.data);
      }
    },

    getIcons() {
      const req = require.context('@/assets/icons/svg', false, /\.svg$/)
      const requireAll = requireContext => requireContext.keys()
      const re = /\.\/(.*)\.svg/
      const arr = requireAll(req).map(i => {
        return i.match(re)[1]
      })
      return arr
    },

    // 
    cancel() {
      this.open = false
      this.reset()
    },

    //
    normalizer(node) {
      if (node.children && !node.children.length) {
        delete node.children
      }
      return {
        id: node.id,
        label: node.menuName,
        children: node.children
      }
    },

    //
    menuTypeChange(val) {
      if (val == 'M') {
        this.form.component = null
        this.form.perms = null
        this.form.path = null
      }
      if (val == 'C') {
        this.form.perms = null
      }
      if (val == 'F') {
        this.form.path = null
        this.form.component = null
        this.form.icon = null
        this.form.status = null
      }
    },

    // 
    reset() {
      this.form = {
        id: null,
        parentId: 0,
        path: null,
        menuName: null,
        icon: null,
        component: null,
        perms: null,
        menuType: 'M',
        orderNum: 0,
        isCache: '0',
        status: '0'
      }
      this.resetForm('form')
    },

    //
    handleAdd() {
      this.reset()
      this.getTreeSelect()
      this.form.parentId = 0
      this.open = true
      this.title = ''
    },
    //
    async getTreeSelect() {
      const {data: res} = await getMenuList()
      this.menuOptions = []
      const menu = {id: 0, menuName: '', children: []}
      menu.children = createTree(res.data, 'id')
      this.menuOptions.push(menu)
    },

    //
    async handleUpdate(row) {
      this.$nextTick(() => {
        this.reset()
      })
      await this.getTreeSelect()
      const {data: res} = await getMenuBy({id: row.id})
      this.form = res.data
      this.open = true
      this.title = ''
    },

    //
    async handleDelete(row) {
      if (row.children && row.children.length > 0) {
        this.$notify.error({
          title: '',
          message: ''
        });
        return
      }

      await deleteMenu({id: row.id})
      this.$notify({
        title: '',
        message: '',
        type: 'success'
      });
      await this.getMenus()
    },

    //
    submitForm() {
      this.$refs['form'].validate(async valid => {
        if (valid) {
          if (this.form.id !== null) {
            //
            await updateMenu(this.form)
            this.$notify({
              title: '',
              message: '',
              type: 'success'
            });

            this.open = false
            await this.getMenus()
          } else {
            //
            await addMenu(this.form)
            this.$notify({
              title: '',
              message: '',
              type: 'success'
            });

            this.open = false
            await this.getMenus()
          }
        }
      })
    },

    //
    resetForm(formName) {
      if (this.$refs.form) {
        this.$nextTick(() => {
          this.$refs[formName].resetFields();
        })
      }
    }
  }
}
</script>

